 */

 .contentoracle-ai_search_root {
	padding: 0.75rem;


	.contentoracle-ai_search_label{
		display: block;
	}
	.contentoracle-ai_search_container{
		display: flex;
		align-items: center;
		padding: 2px;
	}
	.contentoracle-ai_search_input {
		flex-grow: 1;	/* grow to fill the space */
		padding: 1rem 0.4rem;
		font-size: var(--wp--preset--font-size--medium);
		border-width: 0;
	}
	.contentoracle-ai_search_button {
		padding: 1rem;
		font-size: var(--wp--preset--font-size--medium);
		margin-left: 0.25rem;
		border: none;

		&:hover{
			cursor: pointer;
			scale: 1.05;
		}

		&:active{
			scale: 0.95;
		}
	}
}
